import "../../cols.css";
import FinalPolish0 from "../../images/baw/Final Polish 00.png";
import FinalPolish1 from "../../images/baw/Final Polish 01.png";
import FinalPolish2 from "../../images/baw/Final Polish 02.png";
import FinalPolish3 from "../../images/baw/Final Polish 03.png";
import FinalPolish4 from "../../images/baw/Final Polish 04.png";
import FinalPolish5 from "../../images/baw/Final Polish 05.png";
import FinalPolish6 from "../../images/baw/Final Polish 06.png";
import FinalPolish7 from "../../images/baw/Final Polish 07.png";
import FinalPolish8 from "../../images/baw/Final Polish 08.png";
import AbnormalFormation from "../../images/baw/Abnormal Formations.png";
import AncientAltarRoom from "../../images/baw/Ancient Altar Room.png";
import AncientTree from "../../images/baw/Ancient Tree 04.png";
import AstralPlanes from "../../images/baw/Astral Planes_Dreamland.png";
import BuriedStatues from "../../images/baw/Buried Statues.png";
import Beach from "../../images/baw/By the Beach.png";
import Docks from "../../images/baw/By the Docks.png";
import CaveEntrance from "../../images/baw/Cave Entrance.png";
import Cemetery from "../../images/baw/Cemetery.png";
import CrystalCove from "../../images/baw/Crystal Cove.png";
import Woods from "../../images/baw/Deep in the Woods 04.png";
import Drylands from "../../images/baw/Deserted Drylands.png";
import Forest from "../../images/baw/Eldritch Forest.png";
import Flora from "../../images/baw/Exotic Flora.png";
import Fjord from "../../images/baw/Freezing Fjord.png";
import Deities from "../../images/baw/Hall of Deities 04.png";
import IdlePortal from "../../images/baw/Idle Portal.png";
import Inn from "../../images/baw/Inn in the Middle of Nowhere.png";
import Marketplace from "../../images/baw/Marketplace.png";
import MerchantStore from "../../images/baw/Merchant Store.png";
import Nature from "../../images/baw/Nature Untouched 03.png";
import OvergrownRuins from "../../images/baw/Overgrown Ruins.png";
import Water from "../../images/baw/Path Along the Water.png";
import Puzzled from "../../images/baw/Puzzled Platforms 03.png";
import Rocky from "../../images/baw/Rocky Ruins 04.png";
import Shanty from "../../images/baw/Shanty Town.png";
import Summoning from "../../images/baw/Summoning Altar.png";
import Townsquare from "../../images/baw/Townsquare.png";
import UnderwaterTemple from "../../images/baw/Underwater Temple.png";
import Volcanic from "../../images/baw/Volcanic Terrain.png";
import Windmills from "../../images/baw/Windmills and Grasslands.png";
import ImageGrid from "../util/ImageGrid";
import Image from "../util/Image";

const Build = () => {
  const grid1 = [
    FinalPolish0,
    FinalPolish1,
    FinalPolish2,
    FinalPolish3,
    FinalPolish4,
    FinalPolish5,
    FinalPolish6,
    FinalPolish7,
  ];
  const grid2 = [AstralPlanes, IdlePortal, Forest, Fjord];
  const grid3 = [
    Volcanic,
    CrystalCove,
    Summoning,
    Drylands,
    Cemetery,
    Beach,
    Flora,
    Shanty,
    Docks,
    Inn,
    MerchantStore,
    Townsquare,
    Marketplace,
  ];
  const grid4 = [BuriedStatues, AbnormalFormation];
  const grid5 = [
    UnderwaterTemple,
    Water,
    AncientAltarRoom,
    Deities,
    Windmills,
    CaveEntrance,
    Rocky,
    Puzzled,
    Woods,
    Nature,
    AncientTree,
  ];

  return (
    <div className="container mx-auto font-body text-center text-xl flex flex-wrap ">
      <Image image={FinalPolish8} />
      <ImageGrid images={grid1} numCols={4} />
      <div className="mx-auto w-4/5">
        <br />
        <h3>
          For this project I built a world in 31 days with enviornment concepts
          for each day and then at the end I took one of the environments and
          rendered it, which you can see above.
        </h3>
        <br />
      </div>
      <ImageGrid images={grid2} numCols={4} />
      <ImageGrid images={grid3} numCols={3} />
      <ImageGrid images={grid4} numCols={2} />
      <ImageGrid images={grid5} numCols={3} />
      <div className="mt-5 mb-5">
        <Image image={OvergrownRuins} />
      </div>
    </div>
  );
};

export default Build;
