import '../../cols.css'
import MultiGrid from '../util/MultiGrid'
import Image from "../util/Image"
import Ava from "../../images/chars/Ava Magical Girl Concept.png"
import Bot from "../../images/chars/Bot Concepts Sketches and Full.png"
import Pagani from "../../images/chars/Character Concept Art Pagani.png"
import Glow from "../../images/chars/Glow girl.png"
import Cyborg from "../../images/chars/image0.jpeg"
import Tree from "../../images/chars/image1.png"
import Mage from "../../images/chars/image2.png"
import Fairy from "../../images/chars/IMG_0087.PNG"
import Fighter from "../../images/chars/IMG_0149.PNG"
import MageVid from "../../images/chars/Mage vid.mp4"
import MagicVid from "../../images/chars/magic vid.MP4"
import Sketches from "../../images/chars/sketches.jpg"
import Volt from "../../images/chars/Volt Illustration 08.png"
import Zoo from "../../images/chars/Zoo Chinese Restaurant.png"
import TreeVid from "../../images/chars/Automatic.mp4"
import CyborgVid from "../../images/chars/Automatic (1).mp4"
import Promo from "../../images/chars/Dad's Promo Illustration low.png"
import Chocolate from "../../images/chars/JanelleKnight_FinalProject_CharacterConcepts_08.png"
import Dojo from "../../images/chars/JanelleKnight_Project02_10.png"

const Character = () => {
    const grid1 = [
        <Image image={Fairy} />,
        <video autoPlay loop muted>
            <source src={MagicVid} type='video/mp4' />
        </video>,
        <Image image={Bot} />,
        <Image image={Promo} />,
        <Image image={Pagani} />,
        <Image image={Ava} />,
        <video autoPlay loop muted>
            <source src={MageVid} type='video/mp4' />
        </video>,
        <Image image={Mage} />,
        <Image image={Tree} />,
        <video autoPlay loop muted>
            <source src={TreeVid} type='video/mp4' />
        </video>,
        <Image image={Zoo} />,
        <Image image={Volt} />,
        <video autoPlay loop muted>
            <source src={CyborgVid} type='video/mp4' />
        </video>,
        <Image image={Cyborg} />,
        <Image image={Sketches} />,
        <Image image={Glow} />,
        <Image image={Chocolate} />,
        <Image image={Fighter} />,
        <Image image={Dojo} />,
    ]

    return (
        <div className="container mx-auto flex flex-wrap ">
            <MultiGrid items={grid1} numCols={4} />
        </div>
    )
}

export default Character