import React from "react";
import "../../darken.css";
import elmimage from "../../images/portfolio/Portfolio Icons Elm.png";
import spaceimage from "../../images/portfolio/Portfolio Icons space pirates.png"
import mewtationimage from "../../images/portfolio/Portfolio Icons mewtations.png"
import bawimage from "../../images/portfolio/Portfolio Icons build a world in 31 days.png"
import worcesterimage from "../../images/portfolio/Portfolio Icons worcester uprooted.png"
import characterimage from "../../images/portfolio/Portfolio Icons character & enviornment array.png"
import albumimage from "../../images/portfolio/Portfolio Icons album covers.png"
import { NavLink } from "react-router-dom";
import ProjectImage from "../util/ProjectImage";

const Portfolio = () => {
  return (
    <div className="container mx-auto">
      {/* Top Row */} 
      <div className="h-1/2 justify-center flex ">
        <div className=" object-fit w-1/4 mr-5">
          <NavLink to="/elm">
            <ProjectImage project_image={elmimage} />
          </NavLink>
        </div>
        <div className="w-1/4 mr-5">
          <NavLink to="/space">
            <ProjectImage project_image={spaceimage} />
          </NavLink>
        </div>
        <div className="w-1/4 mr-5">
          <NavLink to="/mewtations">
            <ProjectImage project_image={mewtationimage} />
          </NavLink>
        </div>
        <div className="w-1/4 mr-5">
          <NavLink to="/build">
            <ProjectImage project_image={bawimage} />
          </NavLink>
        </div>
      </div>
      {/* Bottom Row */}
      <div className="mt-5 h-1/2 flex justify-center ">
        <div className="w-1/4 mr-5">
          <NavLink to="/worcester">
            <ProjectImage project_image={worcesterimage} />
          </NavLink>
        </div>
        <div className="w-1/4 mr-5">
          <NavLink to="/characters">
            <ProjectImage project_image={characterimage} />
          </NavLink>
        </div>
        <div className="w-1/4 mr-5">
          <NavLink to="/covers">
            <ProjectImage project_image={albumimage} />
          </NavLink>
        </div>
      </div>
    </div>
  );
};

export default Portfolio;
