import Image from "./Image";
import "../../cols.css";

const MultiGrid = ({ items, numCols }) => {
  const col1 = [];
  const col2 = [];
  const col3 = [];
  const col4 = [];
  const col5 = [];

  var setCols = (val) => {
    for (let i = 0; i < val.length; i++) {
      switch (i%numCols) {
        case 0:
          col1.push(val[i])
          break;
        case 1:
          col2.push(val[i])
          break;
        case 2:
          col3.push(val[i])
          break;
        case 3:
          col4.push(val[i])
          break;
        case 4:
          col5.push(val[i])
          break;
        default:
          break;
      }
      
    }
  }
  setCols(items)
  return (
    <div className="flex flex-wrap ">
      <div id="1" style={{flex: numCols >= 1 ? 1 : 0 }} className="column">
        {col1}
      </div>
      <div id="2" style={{flex: numCols >= 2 ? 1 : 0 }} className="column"> 
        {col2}
      </div>
      <div id="3" style={{flex: numCols >= 3 ? 1 : 0 }} className="column">
        {col3}
      </div>
      <div id="4" style={{flex: numCols >= 4 ? 1 : 0 }} className="column">
        {col4}
      </div>
      <div id="5" style={{flex: numCols >= 5 ? 1 : 0 }} className="column">
        {col5}
      </div>
    </div>
  );
};

MultiGrid.defaultProps = {
  numCols: 3,
}

export default MultiGrid